import React from 'react'
import { Card,Table,Button,Popconfirm} from 'antd'

let dataSource;

let url = 'https://www.304cloud.top:4321/getuserlist';
        fetch(url)
        .then(res=>res.json())
        .then(res=>{
            dataSource = res.data
        })


const User = () => {
    const columns = [
        {
            title:'用户id',
            key:'user_id',
            width:250,
            align:'center',
            dataIndex:'user_id'
        },
        {
            title:'用户名',
            dataIndex:'user_name',
            align:'center',
            width:100,
        },
        {
            title:'用户简介',
            dataIndex:'user_self',
            align:'left',
            width:250,
        },
        {
            title:'用户头像',
            width:150,
            dataIndex:'user_head',
            align:'center',
            render:(txt,record) => record.user_head ? (<img src={record.user_head} alt={record.name} style={{width:"40px"}}/>
            ):(
                "暂无图片"
            )
        },
        {
            title:'操作',
            width:150,
            align:'center',
            render:(txt,record,index)=>{
                return(
                    <div>
                        <Button type="primary" size="small">修改</Button>
                        <Popconfirm title="确定删除此项？" 
                        onCancel={()=>console.log('用户取消删除')} 
                        onConfirm={()=>{
                            console.log('用户确定删除');
                            //此处调用删除api
                        }}
                        >
                            <Button style={{margin:"0 0.3rem"}} type="danger" size="small">删除</Button>
                        </Popconfirm>
                       
                    </div>
                )
            }
        },
    ]
    return (
        <Card title="用户列表" extra={<Button type="primary" size="small">添加</Button>}>
            <Table pagination={{defaultPageSize:5}} columns={columns} bordered dataSource={dataSource}/>
        </Card>
    )
}

export default User
