import Index from '../pages/admin/dashboard/Home'
import User from '../pages/admin/User'
import Article from '../pages/admin/Article'
import PageNotFound from '../pages/PageNotFound'
import { TeamOutlined,ProjectOutlined,ReadOutlined,ProfileOutlined,PictureOutlined,MessageOutlined} from '@ant-design/icons';
import Txt from '../pages/admin/Txt';
import Img from '../pages/admin/Img';
import Feedback from '../pages/admin/Feedback';

export const mainRoutes = [
    {
        path:"/404",
        component:PageNotFound
    }
]

export const adminRoutes = [
    {
        path:'/admin/dashboard',
        component:Index,
        isShow:true,
        title:'看板',
        icon:<ProjectOutlined />
    },
    {
        path:'/admin/user',
        component:User,
        exact:true,
        isShow:true,
        title:"用户管理",
        icon:<TeamOutlined />
    },
    {
        path:'/admin/article',
        component:Article,
        exact:true,
        isShow:true,
        title:"作品管理",
        icon:<ReadOutlined />
    },
    {
        path:'/admin/txt',
        component:Txt,
        exact:true,
        isShow:true,
        title:"文字素材管理",
        icon:<ProfileOutlined />
    },
    {
        path:'/admin/img',
        component:Img,
        exact:true,
        isShow:true,
        title:"图片素材管理",
        icon:<PictureOutlined />
    },
    {
        path:'/admin/feedback',
        component:Feedback,
        exact:true,
        isShow:true,
        title:"反馈管理",
        icon:<MessageOutlined />
    }
]